import _ from 'lodash'
import { computed } from 'mobx'
import { useObservable } from 'mobx-react-lite'
import { useCallback, useMemo } from 'react'

import {
  FOrderMenuRecipeFragment,
  QOrderMenuOrderMenu,
} from '../../../graph/generated'
import { isPartialMatch } from '../../../helpers/filter'
import { RM } from '../../../tools/ramda'

interface IFilterState {
  categoryId: MaybeID
  search: string
}

export function useOrderMenuFilter(
  orderMenu: RoA<QOrderMenuOrderMenu> | undefined,
) {
  const observableFilter = useObservable<IFilterState>({
    categoryId: null,
    search: '',
  })

  const computedRecipes = useMemo(
    () =>
      computed(() => {
        if (!orderMenu) {
          return []
        }

        let recipes: RoA<FOrderMenuRecipeFragment>
        let categories = orderMenu

        if (observableFilter.categoryId !== null) {
          categories = orderMenu.filter(
            RM.propEq('id', observableFilter.categoryId),
          )
        }

        if (categories.length === 0) {
          return []
        }

        recipes = _.concat([], ...categories.map(RM.prop('recipes')))

        if (observableFilter.search.length > 0) {
          const partialMatch = isPartialMatch(observableFilter.search)
          return recipes.filter(
            RM.cond([
              [RM.propSatisfies(partialMatch, 'code'), RM.T],
              [RM.propSatisfies(partialMatch, 'nameLabel'), RM.T],
            ]),
          )
        }

        return recipes
      }),
    [observableFilter, orderMenu],
  )

  const toggleCategory = useCallback(
    (categoryId: ID) => {
      if (observableFilter.categoryId === categoryId) {
        observableFilter.categoryId = null
      } else {
        observableFilter.categoryId = categoryId
      }
    },
    [observableFilter],
  )

  const searchByText = useCallback(
    (text: string) => {
      observableFilter.search = text
    },
    [observableFilter],
  )

  return {
    observableFilter,
    computedRecipes,
    toggleCategory,
    searchByText,
  }
}
